<template>
  <div>
    <div class="row justify-content-center">
      <div class="col-md-6">
        <vx-card>
          To start, first write the writing question in the below box, then press start button to start writing. Remember that
          after pressing the start button, a countdown timer will be started and you have 40 minutes to write.

        </vx-card>
      </div>
    </div>
    <div class="row justify-content-center mt-8">
      <div class="col-md-6">
        <vx-card class="ltr">
          <div class="vx-col w-full mb-4">
            <vs-textarea class="writing-question-box" label="writing question" v-model="writingQuestion" style="direction: ltr"/>
            <span v-if="writingQuestionError" class="text-danger text-sm">{{ writingQuestionError }}</span>
          </div>
          <vs-button @click="startWriting">start writing</vs-button>
        </vx-card>
      </div>
    </div>
  </div>
</template>

<script>
import mixins from '../../../mixins';
export default {
  mixins: [mixins],
  name: 'Index',
  data () {
    return {
      writingQuestion: null,
      writingQuestionError: null,
    }
  },

  methods: {
    startWriting () {
      let questionVal = this.validateField(this.writingQuestion, 'question');
      this.writingQuestionError = questionVal.message

      if(!questionVal.validation){
        return;
      }

      this.$router.push({
        name: 'writing-test',
        params: {writingQuestion: this.writingQuestion}
      })
    }
  }
}
</script>

<style scoped>
.vs-textarea {
  height: 300px!important;
}
</style>
